*, *::before, *::after { box-sizing: border-box; }

html { box-sizing: border-box; }

html, body { margin: 0; padding: 0; }

body { width: 100%; height: 100%; font-style: normal; font-weight: 400; font-size: 16px; line-height: 24px; font-family: "Arial", sans-serif; color: #121212; background-color: #ffffff; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

a { color: #3c2a47; text-decoration: none; }

